import React from "react";
import Link from "next/link";
import Layout from "../components/Layout";
import SEO from "../components/head";
import moment from "moment";

import "./post.scss";
import Icon from "../components/Icon";

const template = 'post';

function PostMeta(props) {
  const { date, modified } = props;
  return (
    <div className="post-meta">
      <span>
        <Icon icon={['far', 'calendar']} />Posted on {moment(date).format("YYYY-MM-DD")}
      </span>
    </div>
  );
}

class PostTemplate extends React.Component {
  shouldComponentUpdate() {
    return false;
  }
  render() {
    const post = this.props.page;
    const { previous, next } = this.props.page;

    return (
      <Layout>
        <SEO title={post.frontmatter.title} description={post.frontmatter.description || post.excerpt} />
        <div className="post-content">
          <article className={template} itemScope itemType="http://schema.org/Article">
            <header className={`${template}-header`}>
              <h1>{post.frontmatter.title}</h1>
              <PostMeta date={post.frontmatter.date} modified={post.frontmatter.modified} />
            </header>
            <div 
              className={`${template}-body`} 
              dangerouslySetInnerHTML={{ __html: post.html }} 
              suppressHydrationWarning
            />

          </article>
          
          <hr/>

          <ul
            style={{
              display: `flex`,
              flexWrap: `wrap`,
              justifyContent: `space-between`,
              listStyle: `none`,
              padding: 0
            }}
          >
            <li>
              {previous && (
                <Link to={previous.fields.slug} rel="prev">
                  ← {previous.frontmatter.title}
                </Link>
              )}
            </li>
            <li>
              {next && (
                <Link to={next.fields.slug} rel="next">
                  {next.frontmatter.title} →
                </Link>
              )}
            </li>
          </ul>
        </div>
      </Layout>
    );
  }
}

export default PostTemplate;
